@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family:"Inter",sans-serif;
}

#root {
  margin: 0px;
  padding: 5px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
  flex-wrap: wrap;
}

.profile1-style {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 1px solid white;
  padding: 8px;
  margin-top: 15px;
}

.card-component {
  background-color: rgb(15, 15, 78);
  padding: 15px;
  position: relative;
  text-align: center;
  margin: 0px;
  color: white;
  width:350px;
  height:525px;
}

.status {
  position: absolute;
  top: 10px;
  left: 10px;
  margin: 5px;
  padding: 8px;
  color: black;
}

.online {
  background-color: rgb(8, 152, 8);
}

.offline {
  background-color: orange;
}

.card-component div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10px;
}

.card-component h1 {
  font-size:16px;
  margin-top:10px;
}

.card-component h2 {
  font-size:30px;
  margin-top:10px;
  padding:0px;
}

.card-component p {
  font-size:14px;
}

.card-component div button {
  height: auto;
  padding: 8px 14px;
  color: white;
  margin-top:8px;
}

.msg-btn {
  background-color: rgb(0, 145, 255);
}

.follow-btn {
  background-color: transparent;
  border: 1px solid rgb(0, 145, 255);
}

.card-component ul {
  list-style: none;
  text-align: left;
  margin: 1px;
  padding: 2px;
  width:100%;
}

.card-component ul li {
  border: 1px solid white;
  display: inline-flex;
  margin: 0 7px 7px 0;
  padding: 8px;
  font-size: 12px;
}

.skill-con ul h3 {
  font-size:15px;
}

.skill-con ul li {
  border-color:rgb(2, 84, 147);
}